<template>
  <div class="content">
   <div class="video" v-for="(item,xh) in videoList" :key="xh">
     <video :src="item.playurl" controls></video>
   </div>
  </div>
</template>
<script>
export default {
    name: 'mycode',
    components: {},
    data() {
        return {
            open: false,
            videoList: []
        }
    },
    created() {
        this.getVideo()

    },
    watch: {
        $route() {
        }
    },
    methods: {
        getVideo() {
            fetch('https://api.apiopen.top/api/getMiniVideo').then((rs) => {
                return rs.json()
            }).then((data) => {
                this.videoList = data.result.list
            })
        }
    }
}
</script>
